.Card {
    width:23%;
}
.Card img {
    max-width: 100%;
    height: 200px;
}
.Card button {
    padding: 5px;
    background-color: chocolate;
    border-style: none;
    border-radius:5px;
    cursor: pointer;
}
.Card button:hover {
    background-color: black;
    color: white;
}